@charset "UTF-8";
@import "variables";
@import "../../../sass/func";

.content-guide {
  padding: pxToRem(690px) 0 pxToRem(140px - 92px);

  background: url("../images/bg_guide.jpg") no-repeat top;
  background-color: #fffcf4;
  background-size: pxToRem(750px) pxToRem(592px);
  @at-root .list-guide {
    padding: 0 pxToRem(40px);
    .step {
      display: flex;

      position: relative;

      margin-bottom: pxToRem(92px);
      min-height: pxToRem(260px);

      align-items: center;
    }
    i {
      position: absolute;
      top: 50%;
      left: 0;

      transform: translate(0, -50%);
    }
    .info {
      width: pxToRem(335px);
      height: 100%;
      h1 {
        padding-bottom: pxToRem(14px);

        font-size: pxToRem(36px);

        color: #ff6f18;
      }
      p {
        font-size: pxToRem(26px);
        line-height: pxToRem(26px * 1.5);
        text-align: justify;

        color: #986a2e;
      }
    }
    .left {
      .info {
        padding-left: pxToRem(375px);
      }
    }
    .right {
      i {
        right: 0;
        left: initial;
      }
    }
  }
}
.content-guide-recycle {
    @extend .content-guide;
}

// icons
@mixin step($img, $width) {
  display: block;

  width: pxToRem($width);
  height: pxToRem(260px);

  background: url($img) no-repeat center;
  background-size: contain;
}

.step1 {
  @include step("../images/s1.png", 290px);
}
.step2 {
  @include step("../images/s2.png", 260px);
}
.step3 {
  @include step("../images/s3.png", 290px);
}
.step4 {
  @include step("../images/s4.png", 260px);
}
.step5 {
  @include step("../images/s5.png", 260px);
}
.step6 {
  @include step("../images/s6.png", 286px);
}

.recycle-step1 {
  @include step("../images/guideRecycle/s1.png", 290px);
}
.recycle-step2 {
  @include step("../images/guideRecycle/s2.png", 290px);
}
.recycle-step3 {
  @include step("../images/guideRecycle/s3.png", 260px);
}
.recycle-step4 {
  @include step("../images/guideRecycle/s4.png", 260px);
}
.recycle-step5 {
  @include step("../images/guideRecycle/s5.png", 260px);
}

